.lists {
    
}

.list {    
    border: 1px solid black;    
    margin: 5px;
    padding: 5px;
    position: relative;
    -webkit-animation: showAnimation 1.5s;
            animation: showAnimation 1.5s; 
}

.name {
    text-align: center;
}

@-webkit-keyframes showAnimation {
    0%   { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes showAnimation {
    0%   { opacity: 0; }
    100% { opacity: 1; }
}


