.delete {
    position: absolute;
    right: 0;    
    top: 0;    
    -webkit-transition: 1s;    
    -o-transition: 1s;    
    transition: 1s;
    overflow: hidden;
    height: auto;
    z-index: 1;
}

input[type=submit] {    
    border: none;    
    padding: 5px;
    text-decoration: none;
    margin: 5px 2px;
    cursor: pointer;
}

input[type=submit]:hover {
    background: red;
    color: white;
}



